<template>
  <ApolloQuery
    :query="require('../graphql/queries/getAdminProfile.gql')"
    class="row ma-sm-4"
  >
    <template v-slot="{ result: { error, data }, isLoading }">
      <!-- Loading -->
      <div
        v-if="isLoading === 1"
        class="mx-auto"
      >
        <v-progress-circular
          indeterminate
          color="primary"
        />
      </div>

      <!-- Error -->
      <AppNotification
        v-else-if="error"
        :message="error.message"
      />

      <!-- Result -->
      <template v-else-if="data">
        <v-col v-if="!data.getAdminProfile.permissions.includes('BONUS_ADMINISTRATION') && data.getAdminProfile.role !== 'SUPERUSER'">
          <v-alert
            outlined
            type="error"
          >
            Этот раздел вам недоступен!
          </v-alert>
        </v-col>

        <template v-else>
          <v-col cols="12">
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="dates"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="dateRangeText"
                  solo
                  flat
                  hide-details="auto"
                  background-color="#464646"
                  label="Даты создания"
                  readonly
                  class="rounded-lg"
                  v-on="on"
                >
                  <template v-slot:prepend-inner>
                    <v-icon class="mr-1">
                      mdi-calendar
                    </v-icon>
                  </template>
                </v-text-field>
              </template>
              <v-date-picker
                v-model="dates"
                no-title
                scrollable
                range
              >
                <v-spacer />

                <v-btn
                  text
                  color="primary"
                  @click="menu = false"
                >
                  Отмена
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="saveDates"
                >
                  ОК
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>

          <v-col>
            <ApolloQuery
              :query="require('../graphql/queries/getPromocodes.gql')"
              :variables="{
                start,
                end,
              }"
            >
              <template v-slot="{ result: { error: promocodesError, data: promocodesData }, isLoading: promocodesIsLoading }">
                <!-- Error -->
                <AppNotification
                  v-if="promocodesError"
                  :message="promocodesError.message"
                />

                <!-- Result -->
                <v-card
                  v-else-if="promocodesData || promocodesIsLoading === 1"
                  :loading="promocodesIsLoading === 1"
                  flat
                  class="px-4 pb-2"
                >
                  <template v-if="promocodesData">
                    <v-card-title>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="6"
                        >
                          Промокоды:
                        </v-col>
                        <v-col class="text-right">
                          <ApolloMutation
                            :mutation="require('../graphql/mutations/createPromocode.gql')"
                            :variables="{
                              amount: amount === '' ? null : amount,
                              activationCount: activationCount === '' ? null : activationCount,
                              timeoutSec: timeoutSec === '' ? null : timeoutSec,
                              code,
                            }"
                            :update="updateCache"
                          >
                            <template v-slot="{ mutate, loading, error: createError }">
                              <v-dialog
                                v-model="dialog"
                                persistent
                                max-width="500px"
                              >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-btn
                                    depressed
                                    color="primary"
                                    class="text-none"
                                    large
                                    height="36"
                                    v-bind="attrs"
                                    v-on="on"
                                  >
                                    Создать
                                  </v-btn>
                                </template>

                                <v-card :loading="loading">
                                  <v-card-title>
                                    <span class="text-h5">Создать промокод</span>
                                  </v-card-title>

                                  <v-card-text>
                                    <v-container>
                                      <v-row>
                                        <v-col
                                          cols="12"
                                          sm="6"
                                        >
                                          <v-text-field
                                            v-model.number="amount"
                                            hide-details="auto"
                                            label="Сумма"
                                          />
                                        </v-col>
                                        <v-col
                                          cols="12"
                                          sm="6"
                                        >
                                          <v-text-field
                                            v-model.number="activationCount"
                                            hide-details="auto"
                                            label="Кол-во активаций"
                                          />
                                        </v-col>
                                        <v-col cols="12">
                                          <v-text-field
                                            v-model.number="timeoutSec"
                                            hide-details="auto"
                                            label="Истекает через"
                                            suffix="sec"
                                          />
                                        </v-col>
                                        <v-col cols="12">
                                          <v-text-field
                                            v-model="code"
                                            hide-details="auto"
                                            label="Код"
                                          />
                                        </v-col>
                                      </v-row>
                                    </v-container>
                                  </v-card-text>

                                  <v-card-actions>
                                    <v-spacer />

                                    <v-btn
                                      text
                                      color="primary"
                                      @click="close"
                                    >
                                      Отмена
                                    </v-btn>
                                    <v-btn
                                      text
                                      color="primary"
                                      @click="mutate()"
                                    >
                                      Создать
                                    </v-btn>
                                  </v-card-actions>
                                </v-card>
                              </v-dialog>
                              <AppNotification
                                v-if="createError"
                                :message="createError.message"
                                :icon-disabled="true"
                              />
                            </template>
                          </ApolloMutation>
                        </v-col>
                      </v-row>
                    </v-card-title>
                    <v-data-table
                      :headers="headers"
                      :items="promocodesData.getPromocodes"
                      :items-per-page="5"
                      no-data-text="Промокоды отсутствуют"
                    >
                      <template v-slot:[`item.createdAt`]="{ item }">
                        {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
                      </template>
                    </v-data-table>
                  </template>
                </v-card>

                <!-- No result -->
                <AppNotification
                  v-else
                  message="Нет результата :("
                />
              </template>
            </ApolloQuery>
          </v-col>
        </template>
      </template>

      <!-- No result -->
      <AppNotification
        v-else
        message="Нет результата :("
      />
    </template>
  </ApolloQuery>
</template>

<script>
import AppNotification from '@/components/AppNotification.vue';
import getPromocodes from '@/graphql/queries/getPromocodes.gql';

export default {
  components: {
    AppNotification,
  },
  data() {
    return {
      menu: false,
      dates: [],
      start: '',
      end: '',
      dialog: false,
      amount: '',
      activationCount: '',
      timeoutSec: '',
      code: '',
      headers: [
        { text: 'Сумма:', value: 'amount', class: 'white--text text-subtitle-2' },
        { text: 'Код:', value: 'code', class: 'white--text text-subtitle-2' },
        { text: 'Кол-во активаций:', value: 'activationCounter', class: 'white--text text-subtitle-2' },
        { text: 'Макс. кол-во активаций:', value: 'activationLimit', class: 'white--text text-subtitle-2' },
        { text: 'Истекает:', value: 'expire', class: 'white--text text-subtitle-2' },
        { text: 'Дата создания:', value: 'createdAt', class: 'white--text text-subtitle-2' },
      ],
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
  },
  methods: {
    saveDates() {
      this.$refs.menu.save(this.dates);
      [this.start, this.end] = this.dates;
    },
    updateCache(store, { data: { createPromocode } }) {
      const query = {
        query: getPromocodes,
      };
      // Read the query from cache
      const data = store.readQuery(query);
      // Mutate cache result
      data.getPromocodes.push(createPromocode);
      // Write back to the cache
      store.writeQuery({
        ...query,
        data,
      });

      this.close();
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.amount = '';
        this.activationCount = '';
        this.timeoutSec = '';
        this.code = '';
      });
    },
  },
};
</script>
